<template>
  <div id="app">    
    <div class="box-card">
      <h1 class="white-text">Decoration Simulator</h1>    
    </div>
    <div class="app-container">
      <div class="decoration-images" v-if="true">      
        <small class="subtitle"> > Elige una interior</small>
        <carousel :perPage="4" scrollPerPage="true">
          <slide v-for="(element, index) in images" v-bind:key="index">          
            <img :src="element" :alt="element" v-on:click="changeImage(element)">          
          </slide>              
        </carousel>      
        
      </div>
      <div class="simulator">
        <small class="subtitle"> > Elige un color para pintar el interior</small>
        <div class="simulator-palette">        
          <swatches-picker v-model="colors" @input="updateValue"/>
        </div>
        <DecorationImage v-bind:image="imageDecoration" :color="colorSelected"/>
        <br>
        <br>
        <FishbowlMagikarp  color="#000000" v-if="!true"/>
        <FishbowlMagikarp  color="#03A9F4" v-if="!true"/>
        
      </div>    
    </div>
    <div class="footer">
      <small>@Unicornio Designs :P</small>
    </div>
  </div>
</template>

<script>

import { Swatches } from 'vue-color'
import { Carousel, Slide } from 'vue-carousel'
import FishbowlMagikarp from './components/FishbowlMagikarp'
import DecorationImage from './components/DecorationImage'

const colors = { r: 255, g: 255, b: 255 } 
const images = [
  require('./assets/simulator/house-3.png'),
  require('./assets/simulator/house-2.png'),
  require('./assets/simulator/house-4.webp'),
  require('./assets/simulator/house.png')
]

export default {
  name: 'app',
  components: {        
    'swatches-picker': Swatches,
    Carousel,
    Slide,
    DecorationImage,
    FishbowlMagikarp
  },
  data () {
    return {
      colors,
      images,
      colorSelected: '#FFFFFF',
      imageDecoration: images[0]
    }
  }, 
  methods:{
    updateValue( value){
      
      this.colorSelected = value.hex
    },
    changeImage(value){
      this.imageDecoration = value
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;    
  color: #2c3e50;  
}
.app-container{
  margin: 8px;
}



</style>
